@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body{background-color:black;background-repeat:no-repeat;background-size:103%;font-size:52px;font-family:'Rampart One', Helvetica, sans-serif}.tittle{font-size:72px;margin:50px auto 20px;color:#daf0e3;text-align:center}.stop-watch{display:flex;flex-direction:column;grid-gap:10px;gap:10px;background-color:#daf0e3;margin:0 auto;border:3px solid #b1d4b1;border-radius:30px;width:550px;height:300px}.stop-watch__container{display:flex;justify-content:center;align-items:center}.stop-watch__definitions{display:flex;justify-content:center;flex-direction:row;grid-gap:78px;gap:78px;margin-top:25px}.stop-watch__definition{font-family:Helvetica, sans-serif, Arial}.stop-watch__number{display:flex;justify-content:center;align-items:center;box-sizing:border-box;font-family:'Rampart One';width:140px;height:100px;border-radius:10px;background-color:#fdfdfd;border:2px solid}.stop-watch__data{font-family:Helvetica, sans-serif, Arial;font-weight:800}.stop-watch__separator{font-family:Helvetica, sans-serif, Arial;font-weight:800}.stop-watch__buttons{display:flex;align-items:center;justify-content:center}.stop-watch__button{text-transform:uppercase;font-weight:bold;color:white;margin:5px;height:50px;width:130px}.stop-watch__start{background-color:green}.stop-watch__start:hover{background-color:#0b6d0b;box-shadow:3px 3px 8px 0px rgba(34,60,80,0.2)}.stop-watch__stop{background-color:#e62c2c}.stop-watch__stop:hover{background-color:#d41919;box-shadow:3px 3px 8px 0px rgba(34,60,80,0.2)}.stop-watch__wait{background-color:#c9d610}.stop-watch__wait:hover{background-color:#a8b30f;box-shadow:3px 3px 8px 0px rgba(34,60,80,0.2)}.stop-watch__reset{background-color:#1577f7}.stop-watch__reset:hover{background-color:#125fc4;box-shadow:3px 3px 8px 0px rgba(34,60,80,0.2)}

